<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基础用法1">基础用法</a></li><li><a href="#vue-doc-可勾选1">可勾选</a></li><li><a href="#vue-doc-异步加载子节点1">异步加载子节点</a></li><li><a href="#vue-doc-默认展开选中勾选和禁用1">默认展开、选中、勾选和禁用</a></li><li><a href="#vue-doc-自定义节点内容1">自定义节点内容</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li><li><a href="#vue-doc-iconshapes1">icon&shapes</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-tree树形控件1" class="vue-doc-element">Tree 树形控件</h1>
<p class="vue-doc-element">文件夹、组织架构、生物分类、国家地区等等，世间万物的大多数结构都是树形结构。使用树控件可以完整展现其中的层级关系，并具有展开收起选择等交互功能。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-null1" class="vue-doc-element">null</h1>
<p class="vue-doc-element">null</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<democheckBox></democheckBox>
<demochildNode></demochildNode>
<demodefault></demodefault>
<democustom></democustom>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-treeprops1_1" class="vue-doc-element">Tree props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th style="text-align:right">说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td style="text-align:right">可嵌套的节点属性的数组，生成 tree 的数据</td>
<td>Array</td>
<td>[ ]</td>
</tr>
<tr>
<td>multiple</td>
<td style="text-align:right">是否支持多选</td>
<td>Boolean</td>
<td>Boolean</td>
</tr>
<tr>
<td>show-checkbox</td>
<td style="text-align:right">是否显示多选框</td>
<td>Boolean</td>
<td>Boolean</td>
</tr>
<tr>
<td>empty-text</td>
<td style="text-align:right">没有数据时的提示</td>
<td>没有数据时的提示</td>
<td>暂无数据</td>
</tr>
<tr>
<td>load-data</td>
<td style="text-align:right">异步加载数据的方法，见示例</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>render</td>
<td style="text-align:right">自定义渲染内容，见示例</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>children-key</td>
<td style="text-align:right">定义子节点键</td>
<td>String</td>
<td>String</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-treeevents1_2" class="vue-doc-element">Tree events</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-select-change</td>
<td>点击树节点时触发</td>
<td>当前已选中的节点数组</td>
</tr>
<tr>
<td>on-check-change</td>
<td>点击复选框时触发</td>
<td>当前已勾选节点的数组</td>
</tr>
<tr>
<td>on-toggle-expand</td>
<td>展开和收起子列表时触发</td>
<td>当前节点的数据</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-treemethods1_3" class="vue-doc-element">Tree methods</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>getCheckedNodes</td>
<td>获取被勾选的节点</td>
<td>无</td>
</tr>
<tr>
<td>getSelectedNodes</td>
<td>获取被选中的节点</td>
<td>无</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-children1_4" class="vue-doc-element">children</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th style="text-align:right">说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td style="text-align:right">标题</td>
<td>String / Element String</td>
<td>-</td>
</tr>
<tr>
<td>expand</td>
<td style="text-align:right">是否展开直子节点</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td style="text-align:right">禁掉响应</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>disableCheckbox</td>
<td style="text-align:right">禁掉 checkbox</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>selected</td>
<td style="text-align:right">是否选中子节点</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>checked</td>
<td style="text-align:right">是否勾选(如果勾选，子节点也会全部勾选)</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>children</td>
<td style="text-align:right">子节点属性数组</td>
<td>Array</td>
<td>-</td>
</tr>
<tr>
<td>render</td>
<td style="text-align:right">自定义当前节点渲染内容，见示例</td>
<td>Function</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import democheckBox from './demo/checkBox.md';
import demochildNode from './demo/childNode.md';
import demodefault from './demo/default.md';
import democustom from './demo/custom.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,democheckBox,demochildNode,demodefault,democustom
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


