<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-自定义节点内容1" class="vue-doc-element">自定义节点内容</h2>
<p class="vue-doc-element">使用强大的 Render 函数可以自定义节点显示内容和交互，比如添加图标，按钮等。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-iconshapes1" class="vue-doc-element">icon&amp;shapes</h2>
<p class="vue-doc-element">Button</p>

        </div>

        


        <div class="vue-doc-content">
          <p class="vue-doc-element">Render 函数的第二个参数，包含三个字段：</p>
<ul class="vue-doc-element">
<li>root <code class="vue-doc-element">&lt;Array&gt;</code>：树的根节点</li>
<li>node <code class="vue-doc-element">&lt;Object&gt;</code>：当前节点</li>
<li>data <code class="vue-doc-element">&lt;Object&gt;</code>：当前节点的数据</li>
</ul>
<p class="vue-doc-element">通过合理地使用 root、node 和 data 可以实现各种效果，其中，iView 给每个节点都设置了一个 nodeKey 字段，用来标识节点的 id。</p>
<p class="vue-doc-element">Render 函数分两种，一种是给当前树的每个节点都设置同样的渲染内容，此 render 通过 Tree 组件的属性 render 传递；另一种是单独给某个节点设置，在该节点的 render 字段内设置；同时设置时，会优先使用当前节点的 Render 函数。</p>

        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        
          <div class="vue-doc-demo">
            <template>
    <Tree :data="data5" :render="renderContent"></Tree>
</template>
          </div>
            <div class="vue-doc-code" :class="{'vue-doc-code-show':showVueDocCode}">
              <div class="vue-doc-code-copy" @click="vueDocCodeCopy">{{vueDocCodeCopyText}}</div>
              <div class="vue-doc-code-content" ref="vueDocCodeBox">
                <pre class="vue-doc-element"><code class="lang-vue">&lt;template&gt;
    &lt;Tree :data=<span class="hljs-string">"data5"</span> :render=<span class="hljs-string">"renderContent"</span>&gt;&lt;/Tree&gt;
&lt;/template&gt;
&lt;script&gt;
    export <span class="hljs-keyword">default</span> {
        data () {
            return {
                data5: [
                    {
                        title: <span class="hljs-string">'parent 1'</span>,
                        expand: true,
                        render: (h, { root, node, data }) =&gt; {
                            return h(<span class="hljs-string">'span'</span>, {
                                style: {
                                    display: <span class="hljs-string">'inline-block'</span>,
                                    width: <span class="hljs-string">'100%'</span>
                                }
                            }, [
                                h(<span class="hljs-string">'span'</span>, [
                                    h(<span class="hljs-string">'Icon'</span>, {
                                        props: {
                                            type: <span class="hljs-string">'ios-folder-outline'</span>
                                        },
                                        style: {
                                            marginRight: <span class="hljs-string">'8px'</span>
                                        }
                                    }),
                                    h(<span class="hljs-string">'span'</span>, data.title)
                                ]),
                                h(<span class="hljs-string">'span'</span>, {
                                    style: {
                                        display: <span class="hljs-string">'inline-block'</span>,
                                        float: <span class="hljs-string">'right'</span>,
                                        marginRight: <span class="hljs-string">'32px'</span>
                                    }
                                }, [
                                    h(<span class="hljs-string">'Button'</span>, {
                                        props: Object.assign({}, this.buttonProps, {
                                            icon: <span class="hljs-string">'ios-plus-empty'</span>,
                                            type: <span class="hljs-string">'primary'</span>
                                        }),
                                        style: {
                                            width: <span class="hljs-string">'52px'</span>
                                        },
                                        on: {
                                            click: () =&gt; { this.append(data) }
                                        }
                                    })
                                ])
                            ]);
                        },
                        children: [
                            {
                                title: <span class="hljs-string">'child 1-1'</span>,
                                expand: true,
                                children: [
                                    {
                                        title: <span class="hljs-string">'leaf 1-1-1'</span>,
                                        expand: true
                                    },
                                    {
                                        title: <span class="hljs-string">'leaf 1-1-2'</span>,
                                        expand: true
                                    }
                                ]
                            },
                            {
                                title: <span class="hljs-string">'child 1-2'</span>,
                                expand: true,
                                children: [
                                    {
                                        title: <span class="hljs-string">'leaf 1-2-1'</span>,
                                        expand: true
                                    },
                                    {
                                        title: <span class="hljs-string">'leaf 1-2-1'</span>,
                                        expand: true
                                    }
                                ]
                            }
                        ]
                    }
                ],
                buttonProps: {
                    type: <span class="hljs-string">'ghost'</span>,
                    size: <span class="hljs-string">'small'</span>,
                }
            }
        },
        methods: {
            renderContent (h, { root, node, data }) {
                return h(<span class="hljs-string">'span'</span>, {
                    style: {
                        display: <span class="hljs-string">'inline-block'</span>,
                        width: <span class="hljs-string">'100%'</span>
                    }
                }, [
                    h(<span class="hljs-string">'span'</span>, [
                        h(<span class="hljs-string">'Icon'</span>, {
                            props: {
                                type: <span class="hljs-string">'ios-paper-outline'</span>
                            },
                            style: {
                                marginRight: <span class="hljs-string">'8px'</span>
                            }
                        }),
                        h(<span class="hljs-string">'span'</span>, data.title)
                    ]),
                    h(<span class="hljs-string">'span'</span>, {
                        style: {
                            display: <span class="hljs-string">'inline-block'</span>,
                            float: <span class="hljs-string">'right'</span>,
                            marginRight: <span class="hljs-string">'32px'</span>
                        }
                    }, [
                        h(<span class="hljs-string">'Button'</span>, {
                            props: Object.assign({}, this.buttonProps, {
                                icon: <span class="hljs-string">'ios-plus-empty'</span>
                            }),
                            style: {
                                marginRight: <span class="hljs-string">'8px'</span>
                            },
                            on: {
                                click: () =&gt; { this.append(data) }
                            }
                        }),
                        h(<span class="hljs-string">'Button'</span>, {
                            props: Object.assign({}, this.buttonProps, {
                                icon: <span class="hljs-string">'ios-minus-empty'</span>
                            }),
                            on: {
                                click: () =&gt; { this.remove(root, node, data) }
                            }
                        })
                    ])
                ]);
            },
            append (data) {
                const children = data.children || [];
                children.push({
                    title: <span class="hljs-string">'appended node'</span>,
                    expand: true
                });
                this.$set(data, <span class="hljs-string">'children'</span>, children);
            },
            remove (root, node, data) {
                const parentKey = root.find(el =&gt; el === node).parent;
                const parent = root.find(el =&gt; el.nodeKey === parentKey).node;
                const index = parent.children.indexOf(data);
                parent.children.splice(index, <span class="hljs-number">1</span>);
            }
        }
    }
&lt;/script&gt;
</code></pre>

              </div>
              <div class="vue-doc-code-toggle" @click="vueDocCodeToggle">
                <span class="vue-doc-code-toggle-text"></>{{showVueDocCode?"隐藏代码":"显示代码"}}</span>
                <span class="vue-doc-code-toggle-text-EN"></>{{showVueDocCode?"Hide Code":"Show Code"}}</span>
              </div>
            </div>
          


    </div>
  </template>
  <script>
    
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  export default { mixins: [mixin],
        data () {
            return {
                data5: [
                    {
                        title: 'parent 1',
                        expand: true,
                        render: (h, { root, node, data }) => {
                            return h('span', {
                                style: {
                                    display: 'inline-block',
                                    width: '100%'
                                }
                            }, [
                                h('span', [
                                    h('Icon', {
                                        props: {
                                            type: 'ios-folder-outline'
                                        },
                                        style: {
                                            marginRight: '8px'
                                        }
                                    }),
                                    h('span', data.title)
                                ]),
                                h('span', {
                                    style: {
                                        display: 'inline-block',
                                        float: 'right',
                                        marginRight: '32px'
                                    }
                                }, [
                                    h('Button', {
                                        props: Object.assign({}, this.buttonProps, {
                                            icon: 'ios-plus-empty',
                                            type: 'primary'
                                        }),
                                        style: {
                                            width: '52px'
                                        },
                                        on: {
                                            click: () => { this.append(data) }
                                        }
                                    })
                                ])
                            ]);
                        },
                        children: [
                            {
                                title: 'child 1-1',
                                expand: true,
                                children: [
                                    {
                                        title: 'leaf 1-1-1',
                                        expand: true
                                    },
                                    {
                                        title: 'leaf 1-1-2',
                                        expand: true
                                    }
                                ]
                            },
                            {
                                title: 'child 1-2',
                                expand: true,
                                children: [
                                    {
                                        title: 'leaf 1-2-1',
                                        expand: true
                                    },
                                    {
                                        title: 'leaf 1-2-1',
                                        expand: true
                                    }
                                ]
                            }
                        ]
                    }
                ],
                buttonProps: {
                    type: 'ghost',
                    size: 'small',
                }
            }
        },
        methods: {
            renderContent (h, { root, node, data }) {
                return h('span', {
                    style: {
                        display: 'inline-block',
                        width: '100%'
                    }
                }, [
                    h('span', [
                        h('Icon', {
                            props: {
                                type: 'ios-paper-outline'
                            },
                            style: {
                                marginRight: '8px'
                            }
                        }),
                        h('span', data.title)
                    ]),
                    h('span', {
                        style: {
                            display: 'inline-block',
                            float: 'right',
                            marginRight: '32px'
                        }
                    }, [
                        h('Button', {
                            props: Object.assign({}, this.buttonProps, {
                                icon: 'ios-plus-empty'
                            }),
                            style: {
                                marginRight: '8px'
                            },
                            on: {
                                click: () => { this.append(data) }
                            }
                        }),
                        h('Button', {
                            props: Object.assign({}, this.buttonProps, {
                                icon: 'ios-minus-empty'
                            }),
                            on: {
                                click: () => { this.remove(root, node, data) }
                            }
                        })
                    ])
                ]);
            },
            append (data) {
                const children = data.children || [];
                children.push({
                    title: 'appended node',
                    expand: true
                });
                this.$set(data, 'children', children);
            },
            remove (root, node, data) {
                const parentKey = root.find(el => el === node).parent;
                const parent = root.find(el => el.nodeKey === parentKey).node;
                const index = parent.children.indexOf(data);
                parent.children.splice(index, 1);
            }
        }
    }
</script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


