<template>
    <div class="vue-doc-wrapper">
        
        <div class="vue-doc-title">
          <h2 id="vue-doc-默认展开选中勾选和禁用1" class="vue-doc-element">默认展开、选中、勾选和禁用</h2>
<p class="vue-doc-element">给节点设置 expand、selected、checked 和 disabled 可以将节点设置为展开、选中、勾选和禁用。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h2 id="vue-doc-iconshapes1" class="vue-doc-element">icon&amp;shapes</h2>
<p class="vue-doc-element">Button</p>

        </div>

        


        <div class="vue-doc-content">
          <p class="vue-doc-element">设置属性 multiple 可进行多选。</p>

        </div>
        <div class="vue-doc-content-EN">
          
        </div>
        
          <div class="vue-doc-demo">
            <template>
    <Tree :data="data4" show-checkbox multiple></Tree>
</template>
          </div>
            <div class="vue-doc-code" :class="{'vue-doc-code-show':showVueDocCode}">
              <div class="vue-doc-code-copy" @click="vueDocCodeCopy">{{vueDocCodeCopyText}}</div>
              <div class="vue-doc-code-content" ref="vueDocCodeBox">
                <pre class="vue-doc-element"><code class="lang-vue"><span class="hljs-string">&lt;template&gt;</span>
    <span class="hljs-string">&lt;Tree</span> <span class="hljs-string">:data="data4"</span> <span class="hljs-string">show-checkbox</span> <span class="hljs-string">multiple&gt;&lt;/Tree&gt;</span>
<span class="hljs-string">&lt;/template&gt;</span>
<span class="hljs-string">&lt;script&gt;</span>
    <span class="hljs-string">export</span> <span class="hljs-string">default</span> <span class="hljs-string">{</span>
        <span class="hljs-string">data</span> <span class="hljs-string">()</span> <span class="hljs-string">{</span>
            <span class="hljs-string">return</span> <span class="hljs-string">{</span>
<span class="hljs-attr">                data4:</span> <span class="hljs-string">[</span>
                    <span class="hljs-string">{</span>
<span class="hljs-attr">                        title:</span> <span class="hljs-string">'parent 1'</span><span class="hljs-string">,</span>
<span class="hljs-attr">                        expand:</span> <span class="hljs-literal">true</span><span class="hljs-string">,</span>
<span class="hljs-attr">                        selected:</span> <span class="hljs-literal">true</span><span class="hljs-string">,</span>
<span class="hljs-attr">                        children:</span> <span class="hljs-string">[</span>
                            <span class="hljs-string">{</span>
<span class="hljs-attr">                                title:</span> <span class="hljs-string">'parent 1-1'</span><span class="hljs-string">,</span>
<span class="hljs-attr">                                expand:</span> <span class="hljs-literal">true</span><span class="hljs-string">,</span>
<span class="hljs-attr">                                children:</span> <span class="hljs-string">[</span>
                                    <span class="hljs-string">{</span>
<span class="hljs-attr">                                        title:</span> <span class="hljs-string">'leaf 1-1-1'</span><span class="hljs-string">,</span>
<span class="hljs-attr">                                        disabled:</span> <span class="hljs-literal">true</span>
                                    <span class="hljs-string">},</span>
                                    <span class="hljs-string">{</span>
<span class="hljs-attr">                                        title:</span> <span class="hljs-string">'leaf 1-1-2'</span>
                                    <span class="hljs-string">}</span>
                                <span class="hljs-string">]</span>
                            <span class="hljs-string">},</span>
                            <span class="hljs-string">{</span>
<span class="hljs-attr">                                title:</span> <span class="hljs-string">'parent 1-2'</span><span class="hljs-string">,</span>
<span class="hljs-attr">                                expand:</span> <span class="hljs-literal">true</span><span class="hljs-string">,</span>
<span class="hljs-attr">                                children:</span> <span class="hljs-string">[</span>
                                    <span class="hljs-string">{</span>
<span class="hljs-attr">                                        title:</span> <span class="hljs-string">'leaf 1-2-1'</span><span class="hljs-string">,</span>
<span class="hljs-attr">                                        checked:</span> <span class="hljs-literal">true</span>
                                    <span class="hljs-string">},</span>
                                    <span class="hljs-string">{</span>
<span class="hljs-attr">                                        title:</span> <span class="hljs-string">'leaf 1-2-1'</span>
                                    <span class="hljs-string">}</span>
                                <span class="hljs-string">]</span>
                            <span class="hljs-string">}</span>
                        <span class="hljs-string">]</span>
                    <span class="hljs-string">}</span>
                <span class="hljs-string">]</span>
            <span class="hljs-string">}</span>
        <span class="hljs-string">}</span>
    <span class="hljs-string">}</span>
<span class="hljs-string">&lt;/script&gt;</span>
</code></pre>

              </div>
              <div class="vue-doc-code-toggle" @click="vueDocCodeToggle">
                <span class="vue-doc-code-toggle-text"></>{{showVueDocCode?"隐藏代码":"显示代码"}}</span>
                <span class="vue-doc-code-toggle-text-EN"></>{{showVueDocCode?"Hide Code":"Show Code"}}</span>
              </div>
            </div>
          


    </div>
  </template>
  <script>
    
  
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      
    }
  };
  export default { mixins: [mixin],
        data () {
            return {
                data4: [
                    {
                        title: 'parent 1',
                        expand: true,
                        selected: true,
                        children: [
                            {
                                title: 'parent 1-1',
                                expand: true,
                                children: [
                                    {
                                        title: 'leaf 1-1-1',
                                        disabled: true
                                    },
                                    {
                                        title: 'leaf 1-1-2'
                                    }
                                ]
                            },
                            {
                                title: 'parent 1-2',
                                expand: true,
                                children: [
                                    {
                                        title: 'leaf 1-2-1',
                                        checked: true
                                    },
                                    {
                                        title: 'leaf 1-2-1'
                                    }
                                ]
                            }
                        ]
                    }
                ]
            }
        }
    }
</script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


